import Bmob from "hydrogen-js-sdk";

import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

let that;
let search_text;
let uid;
export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			showOrder: false,
			loading: true,
			staffs: [],
			is_choose: false,
			disabled: false,

			staffHeader: {
				num: 0,
				debtMoney: 0
			},

			headerSelection: {
				order: {
					"order": "-createdAt"
				},
				options: '',
			},
			orders: [{
				"desc": "创建日期",
				"notice": "最新",
				"order": "-createdAt",
				"checked": true,
			}, {
				"desc": "创建日期",
				"notice": "最晚",
				"order": "createdAt",
				"checked": false,
			}, {
				"desc": "名字",
				"notice": "正序",
				"order": "goodsName",
				"checked": false,
			}]
		}
	},

	onLoad(options) {
		that = this;
		uni.removeStorageSync("")
		uid = uni.getStorageSync('uid')
		console.log(options)
		if (options.type == "choose") {
			that.is_choose = true
		}
	},
	onShow() {
		uni.removeStorageSync("staff")
		that.getstaff_list()
	},
	onUnload() {
		search_text = ""
	},
	methods: {

		//头部的options选择
		selectd(type) {
			if (type == "order") {
				that.showOrder = true
			} else if (type == "options") {
				that.showOptions = true
			}
		},

		//选择当前排序
		selectOrder(item) {
			for (let item of that.orders) {
				item.checked = false
			}
			item.checked = true
			that.headerSelection.order = item
			that.getstaff_list();
		},

		//打电话
		makePhoneCall(phone) {
			if (phone) {
				uni.makePhoneCall({
					phoneNumber: phone //仅为示例
				});
			} else {
				uni.showToast({
					icon: "none",
					title: "未填写联系电话"
				})
			}
		},

		//去到员工详情
		goto_detail(staff) {
			if (that.is_choose) {
				let charge = staff;
				uni.setStorageSync("charge", charge);
				uni.navigateBack({
					delta: 1
				})
			}else{
				uni.setStorageSync("staff", staff)
				uni.navigateTo({
					url: "detail/detail"
				})
			}
		},

		//选择此员工当负责人
		/*select_this(e) {
			let charge = JSON.parse(e.detail.value);
			uni.setStorageSync("charge", charge);
			uni.navigateBack({
				delta: 1
			})
		},*/

		//前去添加员工
		goto_add() {
			let user = uni.getStorageSync("user")
			let identity = uni.getStorageSync("identity")
			if (user.is_vip || that.staffs.length < 2) {
				uni.navigateTo({
					url: "add/add"
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '非会员最多上传2个员工账号',
					confirmText: "充值会员",
					success: function(res) {
						if (res.confirm) {
							if (identity == 1) {
								uni.navigateTo({
									url: "/pages/mine/vip/vip"
								})
							} else {
								uni.showToast({
									title: "员工不能充值",
									icon: "none"
								})
							}
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			}

		},

		//输入内容筛选
		input_confirm(e) {
			search_text = e.detail.value
			that.getstaff_list();
		},

		//得到员工列表
		getstaff_list: function() {
			const query = Bmob.Query("_User");
			query.order("-createdAt");
			query.equalTo("masterId", "==", uid);
			if (that.disabled) {
				query.equalTo("disabled", "==", that.disabled);
			}
			query.include("shop");
			query.order(that.headerSelection.order.order);
			if (search_text) {
				query.equalTo("username", "==", {
					"$regex": "" + search_text + ".*"
				});
			}
			query.find().then(res => {
				//console.log(res)
				that.loading = false;
				that.staffs = res;
			});
		},

	}
}
